import React, {useState } from 'react';

export function AddComment(props){

    const [userName, setUserName] = useState('');
    const [text, setText] = useState('');
    
    const AddComment = async (articleName, addComment) => {
        await fetch(`/api/articles/${articleName}/addcomment`, {
            method: "post",
            body: JSON.stringify({ userName: userName, text: text }),
            headers: {
                'Content-Type': 'application/json',
            }
        })
        .then(res => res.json())
        .then(result => addComment(result.upvotes, result.comments))

        setUserName('');
        setText('');
    }

    return(
        <div id="add-comment-form">
            <h3>Add Comments:</h3>
            <label>
                Name: 
                <input type='text' value={userName} 
                       onChange={(e) => setUserName( e.target.value )} />
            </label>
            <label>
                Comment: 
                <textarea rows='4' cols='50' value={text} 
                          onChange={(e) => setText( e.target.value )} />
            </label>
            <button name={props.articleName} onClick={(e) => AddComment(e.target.name, props.addComment)}>Add Comment</button>
        </div>
    );
}