import React from 'react';

export function Upvotes (props){
    const AddVote = async (articleName, addVote) => {
        await fetch(`/api/articles/${articleName}/upvote`, {
            method: "post",
        })
        .then(res => res.json())
        .then(result => addVote(result.upvotes, result.comments))
    }

    return(
    <div id='upvotes-section'>
        <button type='button' name={props.articleName} 
                onClick={(e) => AddVote(e.target.name, props.addVote)}>Add vote</button>
        <h4> This post has been upvotes {props.upvotes} times </h4>
    </div>
)
};