import React, { useEffect, useState } from 'react';
import ArticlesList from '../components/Articles/ArticlesList';
import NotFoundPage from './NotFoundPage';
import articleContent from './article-content';
import { Upvotes } from '../components/Votes/UpvotesSection';
import { CommentSection } from '../components/Comments/CommentSection';
import { AddComment } from '../components/Comments/AddComment';

const ArticlePage = ({ match }) => {
    const name = match.params.name;
    const article = articleContent.find(article => article.name === name);

    const [articleInfo, setArticleInfo] = useState({ upvotes: 0, comments: [] });

    useEffect(() => {
        fetch(`/api/articles/${name}/`)
        .then(res => res.json())
        .then(result => setArticleInfo(result))
    }, [name]);

    if (!article) return <NotFoundPage />

    const otherArticles = articleContent.filter(article => article.name !== name);
    console.log(articleInfo);
    return (
        <>
            <h1>{article.title}</h1>
            <Upvotes upvotes={articleInfo.upvotes} articleName={name} 
                     addVote={(upvotes, comments) => setArticleInfo({upvotes: upvotes, comments: comments})}/>
            {article.content.map((paragraph, key) => (
                <p key={key}>{paragraph}</p>
            ))}
            <CommentSection comments={articleInfo.comments} />
            <AddComment articleName={name}
                        addComment={(upvotes, comments) => setArticleInfo({upvotes: upvotes, comments: comments})}/>
            <h3>Other Articles:</h3>
            <ArticlesList articles={otherArticles} />
        </>
    );
}

export default ArticlePage;