import React from 'react';

export function CommentSection({ comments }){
    return(
        <>
        <h3>Comments:</h3>
        {
            comments.map((cmnt, key) => 
                <div className="comment" key={key}>
                    <h4>{cmnt.userName}</h4>
                    <p>{cmnt.text}</p>
                </div>
            )
        }
        </>
    );
}